@import "normalizes.scss";
@import "resets.scss";
* {
    box-sizing: border-box; }

@font-face {
    font-family: "NunitoO";
    src: url('../font/Nunito-Bold.ttf');
    font-weight: bold;
    font-style: normal; }


@font-face {
    font-family: "MontserratT";
    src: url('../font/Montserrat-Regular.ttf');
    font-weight: regular;
    font-style: normal; }

$color-blue: #5290EA;
$color-orange: #F28B64;

.container {
    max-width: 1140px;
    margin: 0 auto; }
.main-section {
    position: relative;
    min-height: 800px;
    background: #F2F2F2;
    overflow: hidden;
    margin-bottom: 30px;
    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        font-family: "NunitoO";
        font-weight: 700; }
    &__logo {
        width: 198px; }
    &__content-container {
        position: relative;
        display: flex;
        flex-direction: row; }
    &__subtract {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 231px;
        height: 231px;
        bottom: -110px;
        right: -80px;
        background: linear-gradient(149.95deg, rgba(235, 164, 61, 0.5) 14.25%, rgba(241, 124, 84, 0.5) 47.33%, rgba(76, 29, 95, 0.5) 86.43%);
        transform: rotate(76.28deg);
        border-radius: 200px; }
    &__subtract-box-2 {
        position: absolute;
        width: 156.35px;
        height: 156.35px;


        background: #F0F0F0;
        transform: rotate(-163.02deg);
        border-radius: 200px; } }


.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__list-item {
        margin-right: 100px;
        font-size: 19px;
        &:last-child {
            margin-right: 0px; }
        a {
            text-decoration: none;
            color: black; } } }

.left-wrapper {
    position: relative;
    img {
        position: relative;
        z-index: 5; } }
.rectangle {
    &__yellow {
        position: absolute;
        top: 145px;
        left: -77px;
        width: 579.95px;
        height: 220px;
        background: #EBA642;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-62.51deg);
        z-index: 2; }
    &__orange {
        position: absolute;
        width: 660.43px;
        height: 220px;
        left: -50px;
        top: 160px;
        background: #EE8764;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-45deg);
        z-index: 3; }
    &__purple {
        position: absolute;
        width: 608.97px;
        height: 220px;
        left: 16px;
        top: 200px;
        background: #5D376C;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-28.48deg);
        z-index: 1; } }

.right-wrapper {
    width: 372px;
    position: absolute;
    top: 150px;
    right: 20px;
    z-index: 5;
    &__icons-box {
        margin-bottom: 1px;
        padding-left: 10px; }
    &__title {
