        border: none;
        background: #F17C54;
        border-radius: 100px;
        margin-right: 30px;
        color: white;
        font-size: 18px;
        font-family: "MontserratT";
        font-weight: regular;
        p {
            position: absolute;
            text-align: center;
            font-family: "MontserratT";
            font-weight: regular; } }
    &__chat {

        display: inline-flex;
        align-items: center;
        img {

            margin-right: 10px; }
